import { defineComponent, ref } from "vue";
import { AxForm } from "@/base-ui/form/index";
export default defineComponent({
  props: {
    searchFormConfig: {
      type: Object,
      required: true
    }
  },

  setup(props, {
    emit
  }) {
    // 根据定义属性生成对应空值对象
    const formItems = props.searchFormConfig?.formItems || [];
    const formDataOrigin = {};

    for (const key of formItems) {
      formDataOrigin[key.field] = "";
    } // 生成响应式输出对象


    const formData = ref(formDataOrigin); // AxForm实例

    const axForm = ref(); // 用户点击重置

    const handleReset = () => {
      if (axForm.value) {
        for (const key of formItems) {
          axForm.value.formData[key.field] = formDataOrigin[key.field];
        }
      }

      emit("resetBtn");
    }; // 用户点击搜索


    const handleSearch = () => {
      // 发送当前formdata, 搜索第一页数据
      emit("searchBtn", formData.value);
    };

    return {
      formData,
      handleSearch,
      handleReset,
      axForm
    };
  },

  components: {
    AxForm
  }
});